
.todo-container {
    text-align: center;
    background-color: #282c34;
    color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 20px;
    background-color: #1a1a1a;
    z-index: 1000; 
  }
  
  .navbar-title {
    font-size: 1.5rem;
  }
  
  .navbar-user {
    display: flex;
    align-items: center;
  }
  
  .profile-picture {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 100px;
  }
  
  .user-email {
    margin-right: 10px;
  }
  
  .sign-out-button {
    padding: 10px;
    background-color: #61dafb;
    border: none;
    cursor: pointer;
    border-radius: 20px;
  }
  
  .sign-out-button:hover {
    background-color: #21a1f1;
  }
  
  .add-todo-form {
    margin-top: 80px; 
  }
  
  ul {
    list-style: none;
    padding: 0;
    overflow-y: auto; 
    max-height: 335px;
    width: 100%; 
  }
  
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    margin: 10px 0;
    background-color: #444;
    border-radius: 4px;
  }
  
  li button {
    margin-left: 10px;
  }
  
  li[style*="line-through"] {
    color: #888;
  }
  
  .search-container {
    position: relative;
    display: inline-block;
    margin-bottom: 10px;
  }
  
  .search-container input {
    padding-right: 30px;
  }
  
  .clear-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    color: red;
  }
  